.homepage-container{width:100%;display:flex;align-items:center;flex-direction:column}.homepage-container .homepage-text{background-color:#ffdf99;padding:50px;margin-top:50px;width:50%;display:flex;flex-direction:column;justify-content:space-around;text-align:justify}@media (max-width: 768px){.homepage-container .homepage-text{width:85%;padding:15px;margin-top:0;margin-bottom:20px}}.homepage-container .homepage-text::before{position:absolute;margin-left:200px;transform:rotate(30deg);width:250px;height:450px;content:"";background-image:url("https://firebasestorage.googleapis.com/v0/b/ev-otomasyon-f0c18.appspot.com/o/assets%2Fesp8266_nodemcu.png?alt=media&token=b8205306-842a-4599-bc10-191ac7045293");background-size:cover;opacity:0.1}@media (max-width: 768px){.homepage-container .homepage-text::before{display:none}}.homepage-container .homepage-text .first-h2{text-align:right}

.chart-infos{width:100%;height:100%;display:flex;flex-direction:column}.chart-infos .info-header{background-color:#f3efef;height:60px;display:flex;align-items:center;justify-content:space-evenly}.chart-infos .info-header .latest-value{font-size:20px}.chart-infos .info-chart{height:100%;width:100%;display:flex;justify-content:center;align-items:center}

